import React, { useState, useEffect } from 'react'
import { NavLink } from 'react-router-dom'
import {
  CContainer,
  CHeader,
  CHeaderBrand,
  CHeaderNav,
  CNavLink,
  CNavItem,
  CDropdown,
  CDropdownToggle,
  CDropdownMenu,
  CDropdownItem,
  CDropdownDivider
} from '@coreui/react'
import CIcon from '@coreui/icons-react'

import { logo_bde_bleu } from 'src/assets/logo_bde_bleu'
import { logo_bde_blanc } from 'src/assets/logo_bde_blanc'

let logo_bde = logo_bde_bleu

function AppHeader() {
  const [stickyClass, setStickyClass] = useState('');
  const [colorClass, setColorClass] = useState('');

  useEffect(() => {
    window.addEventListener('scroll', stickNavbar);
    return () => window.removeEventListener('scroll', stickNavbar);
  }, []);

  const stickNavbar = () => {
      let windowHeight = window.scrollY;
      if (windowHeight > 150) {
        setStickyClass('sticky-nav')
        setColorClass('sticky-text')
        logo_bde = logo_bde_blanc
      }
      else {
        setStickyClass('')
        setColorClass('')
        logo_bde = logo_bde_bleu
      }
  };

  return (
    <CHeader position="fixed" className={ `navbar ${stickyClass}`}>
      <CContainer fluid>

        <CHeaderBrand className="mx-auto logo_lg">
          <CIcon icon={logo_bde} height={92} />
        </CHeaderBrand>
        
        <CHeaderNav className="d-none d-lg-flex nav-items">

        <CNavItem>
            <CNavLink to="/dashboard" component={NavLink} className={`${colorClass}`}>
              Dashboard
            </CNavLink>
        </CNavItem>

        <CNavItem>
            <CNavLink href="#" className={`${colorClass}`}>Users</CNavLink>
        </CNavItem>

        <CNavItem>
            <CNavLink href="#" className={`${colorClass}`}>Settings</CNavLink>
        </CNavItem>

        <CDropdown component="li" variant="nav-item">
              <CDropdownToggle className={`${colorClass}`}>Dropdown</CDropdownToggle>
                <CDropdownMenu>
                  <CDropdownItem href="#">Action</CDropdownItem>
                  <CDropdownItem href="#">Another action</CDropdownItem>
                  <CDropdownItem href="#">Something else here</CDropdownItem>
                  <CDropdownDivider />
                  <CDropdownItem href="#">Separated link</CDropdownItem>
                </CDropdownMenu>
          </CDropdown>

        </CHeaderNav>

      </CContainer>
    </CHeader>
  )
}

export default AppHeader
